import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		// 是否需要强制登录
		num:1,
		forcedLogin: false,
		hasLogin: false,
		IsStaff:false,
		IsEnterprise:false,
		IsPrincipal:false,
		EnterpriseId:'',
		userName: "",
		userPhone:"",
		usertoken:"",
		userId:"",
		userAge:'',
		EnterpriseName:"",
		EnterpriseMoney:0,
	},
	mutations: {
		login(state, user) {
			state.userName = user.userName;
			state.userPhone=user.userPhone;
			state.usertoken=user.token || '';
			state.userId=user.id || '';
			state.hasLogin = true;
			if(user.eName!=null){
				state.IsEnterprise=true;
			}
			state.EnterpriseName=user.eName;
			state.EnterpriseId=user.eId
			if(!(user.Principal==null))
			{
				state.IsPrincipal=true
				state.EnterpriseMoney=user.eMoney;
			}
			uni.setStorage({
				key:'userInfo',
				data:user
			})
		},
		// Enterprise(state,eUser){
		// 	state.EnterpriseName=user.eName;
		// 	state.EnterpriseId=user.eId
		// },
		// Staf
		StaffCertify(state, enterprise){
			state.IsStaff=true;
			state.EnterpriseId=enterprise.id;
			state.EnterpriseName=enterprise.enterprisename||'暂未与企业绑定';//enterprise.enterprisename
			if(state.EnterpriseName=='暂未与企业绑定'){
				state.IsEnterprise=false;
			}
			else{
				state.IsEnterprise=true
			}
			uni.setStorage({
				key:'enterpriseInfo',
				data:enterprise
			})
		},
		changeNum(state){
			state.num=2;
		},
		logout(state) {
			state.userName="";
			state.userPhone=' ';
			state.usertoken=' ';
			state.userId=' ';
			state.hasLogin = false;
			state.IsStaff=false;
			state.EnterpriseName='';
			state.EnterpriseId='';
			state.EnterpriseMoney=0;
			state.IsPrincipal=false;
			state.isEnterprise=false;
			uni.removeStorage({
				key:'userInfo'
			})
			uni.removeStorage({
				key:'enterpriseInfo'
			})
		},
		
	}
})

export default store
